<template>
<div class="row">
    <div class="col-12">
        <div class="card border-0">
            <div class="card-body">
                <h1 class="fw-bold title-page">{{ title }}</h1>
                <div class="row">
                    <div class="col-md-4 col-sm-12">
                        <div class="form-group">
                            <label for="">Tên công ty</label>
                            <input type="text" class="form-control" v-model="Company.company_name" placeholder="Nhập tên công ty" />
                        </div>
                    </div>
                    <div class="col-md-4 col-sm-12">
                        <div class="form-group">
                            <label for="">Mã số thuế</label>
                            <div class="input-group">
                                <input type="text" class="form-control" v-model="Company.company_taxcode" placeholder="Nhập mã số thuế công ty" />
                                <button @click="getInfo()" class="btn btn-primary-soft" type="button" id="button-addon1">
                                    Lấy thông tin
                                </button>
                            </div>
                            <small>{{ company_type }}</small>
                        </div>
                    </div>

                    <div class="col-md-4 col-12">
                        <div class="form-group">
                            <label for="">Ngày thành lập</label>
                            <date-picker v-model="Company.company_date_create" :config="options"></date-picker>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-12">
                        <div class="form-group">
                            <label for="">Địa chỉ công ty</label>
                            <input type="text" v-model="Company.company_address" placeholder="Địa chỉ công ty" class="form-control" />
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-6">
                        <div class="form-group">
                            <label for="">Cơ quan thuế quản lý</label>
                            <input type="text" v-model="Company.company_tax_manager" placeholder="Cơ quan thuế quản lý" class="form-control" />
                        </div>
                    </div>
                    <div class="col-6">
                        <div class="form-group">
                            <label for="">Ngành nghề kinh doanh</label>
                            <input type="text" v-model="Company.company_type" placeholder="Ngành nghề kinh doanh" class="form-control" />
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-4">
                        <div class="form-group">
                            <label for="">Nơi cấp MST</label>
                            <input type="text" placeholder="Nơi cấp mã số thuế" v-model="Company.taxcode_issuedby" class="form-control">
                        </div>
                    </div>

                    <div class="col-4">
                        <div class="form-group">
                            <label for="">STK ngân hàng</label>
                            <input type="text" placeholder="Số tài khoản ngân hàng" v-model="Company.bankcode" class="form-control">
                        </div>
                    </div>

                    <div class="col-4">
                        <div class="form-group">
                            <label for="">Tên ngân hàng</label>
                            <input type="text" placeholder="Tên ngân hàng" v-model="Company.bankname" class="form-control">
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-4">
                        <div class="form-group">
                            <label for="">Người đại diện</label>
                            <input type="text" v-model="Company.company_owner" placeholder="Người đại diện" class="form-control" />
                        </div>
                    </div>
                    <div class="col-4">
                        <div class="form-group">
                            <label for="">Chức vụ</label>
                            <input type="text" v-model="Company.company_position" placeholder="Chức vụ" class="form-control" />
                        </div>
                    </div>
                    <div class="col-2">
                        <div class="form-group">
                            <label>Giới tính</label>
                            <select class="form-select" v-model="Company.company_sex">
                                <option v-for="option in sex" :value="option.value" :key="option.value">
                                    {{ option.text }}
                                </option>
                            </select>
                        </div>
                    </div>

                    <div class="col-2">
                        <div class="form-group">
                            <label>Ngày sinh</label>
                            <!-- <input type="text" v-model="Company.owner_birthday" class="form-control" placeholder="Ngày sinh" /> -->
                            <date-picker v-model="Company.owner_birthday" :config="options"></date-picker>
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-4">
                        <div class="form-group">
                            <label for="">Điện thoại cố định</label>
                            <input type="text" class="form-control" v-model="Company.company_phone" placeholder="Nhập Điện thoại cố định" />
                        </div>
                    </div>
                    <div class="col-4">
                        <div class="form-group">
                            <label>Điện thoại di động</label>
                            <input type="text" class="form-control" v-model="Company.company_telephone" placeholder="Nhập Điện thoại di động" />
                        </div>
                    </div>
                    <div class="col-4">
                        <div class="form-group">
                            <label>Email</label>
                            <input type="text" class="form-control" v-model="Company.company_email" placeholder="Nhập email người đại diện" />
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-4">
                        <div class="form-group">
                            <label>Số CMND/CCCD</label>
                            <input type="text" class="form-control" v-model="Company.company_card_id" placeholder="Nhập Số CMND/CCCD" />
                        </div>
                    </div>
                    <div class="col-4">
                        <div class="form-group">
                            <label>Ngày cấp CMND/CCCD</label>
                            <input type="text" class="form-control" v-model="Company.company_card_date" placeholder="Ngày cấp CMND/CCCD" />
                        </div>
                    </div>
                    <div class="col-4">
                        <div class="form-group">
                            <label>Nơi cấp CMND/CCCD</label>
                            <input type="text" class="form-control" v-model="Company.company_card_issued" placeholder="Nơi cấp CMND/CCCD" />
                        </div>
                    </div>
                </div>
            </div>
            <div class="card-footer border-top-0">
                <router-link :to="{ name: 'companies' }" class="btn btn-outline-secondary me-2" tag="a">
                    <i class="fa-solid fa-arrow-left"></i> Quay lại</router-link>
                <button :disabled="isDisableStore" @click="store()" class="btn btn-primary" title="Lưu dữ liệu">
                    <i class="fa-solid fa-floppy-disk"></i> Lưu
                </button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from "axios";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";

export default {
    components: {
        datePicker,
    },
    data() {
        return {
            infoCompany: [],
            company_id: 0,
            company_type: "",
            title: "Tạo mới công ty",
            Company: {
                company_name: "",
                company_address: "",
                company_telephone: 0,
                company_phone: 0,
                company_owner: "",
                company_tax_manager: "",
                company_email: "",
                company_position: "",
                company_tax_manager: "",
                company_sex: "Ông",
                company_type: "",
                company_date_create: this.$econtract.formatDate(new Date()),
                owner_birthday: "01/01/1970",
            },
            isDisableStore: false,
            isUpdate: false,
            sex: [{
                    text: "Ông",
                    value: "Ông",
                },
                {
                    text: "Bà",
                    value: "Bà",
                },
            ],
            options: {
                format: "DD/MM/YYYY",
                useCurrent: false,
            },
        };
    },
    created() {
        if (this.$route.params.id > 0) {
            this.title = "Cập nhật công ty";
            this.company_id = this.$route.params.id;
            this.isUpdate = true;
            this.show();
        }
    },
    methods: {
        async store() {
            this.$econtract.showLoading();
            if (!this.isUpdate) {
                await axios.post(`/companies`, this.Company).then((response) => {
                    if (response.status == 200) {
                        if (response.data.error == 404) {
                            this.$toast.error(response.data.msg);
                            this.$econtract.hideLoading();
                        } else {
                            this.$router.push("/companies/");
                            this.$toast.success("Thêm mới thành công");
                            this.$econtract.hideLoading();
                        }
                    }
                });
            } else {
                await axios.put(`${API_URL}/companies/update`, this.Company).then((response) => {
                    this.$router.push("/companies/");
                    this.$econtract.hideLoading();
                    this.$toast.success("Cập nhật thành công");
                });
            }
        },
        async show() {
            this.$econtract.showLoading();
            const response = await axios.get(`/companies/${this.company_id}`);
            this.Company.company_id = this.company_id;
            this.Company = response.data;
            this.Company.company_date_create = this.$econtract.formatDate(
                response.data.company_date_create
            );
            this.Company.company_card_date = this.$econtract.formatDate(
                response.data.company_card_date
            );
            this.Company.owner_birthday = this.$econtract.formatDate(
                response.data.owner_birthday
            );
            this.$econtract.hideLoading();
        },
        getInfo() {
            this.$econtract.showLoading();
            axios
                .post(`/companies/getInfo`, {
                    taxcode: this.Company.company_taxcode,
                })
                .then((response) => {
                    let result = JSON.parse(response.data);

                    if (result.Data.length > 0) {
                        let resultCompany = JSON.parse(result.Data);
                        this.infoCompany = resultCompany
                        this.company_type = resultCompany.activeStatus
                        if (result.Success) {
                            this.Company.company_taxcode = resultCompany.taxCode;
                            this.Company.company_name = resultCompany.companyName;
                            this.Company.company_date_create = this.$econtract.formatDate(
                                resultCompany.registedDate
                            );
                            this.Company.company_address = resultCompany.address;
                            this.Company.company_tax_manager = resultCompany.businessResistration;
                            this.Company.company_type = resultCompany.businessType;
                            this.Company.company_email = resultCompany.email;
                            this.Company.company_owner = resultCompany.owner;
                            this.Company.company_telephone = resultCompany.contactPhoneNumber;
                            this.Company.company_position = resultCompany.ownerPosition;
                            this.$econtract.hideLoading();
                        }
                    } else {
                        this.$toast.warning("Không tìm thấy thông tin công ty");
                        this.$econtract.hideLoading();
                    }
                });
        },
    },
};
</script>
